import axios from '../utils/axios';

export default class ToDos {
  //Criando a requisio get, para exibir todos os ToDos:
  async list() {
    const { data } = await axios.get('/ToDos');
    //Retorna os ToDos, presentes no db.json:
    return data;
  }

  //Criando a requisio post, para adicionar um novo ToDo:
  async store({ text, done }) {
    const { data } = await axios.post('/ToDos', { text, done });
    return data;
  }

  //Criando a requisio put, para atualizar os ToDos:
  async update({ id, text, done }) {
    const { data } = await axios.put(`/ToDos/${id}`, { text, done });
    return data;
  }

  //Criando a requisio delete, para deletar os ToDos:
  async delete({ id }) {
    await axios.delete(`/ToDos/${id}`);
  }
}
